import './style.css';


const myDom = (function(){
    const options = document.querySelector('.options');
    const filter_btn = document.querySelector('.filter');
    const fa_angle_up = document.querySelector('.filter i');
    const filter_input = document.querySelector('.filter-input');
    const country_card = document.querySelector('.country-card');
    const mode= document.querySelector('.mode');

    return{
        options,
        filter_btn,
        fa_angle_up,
        filter_input,
        country_card,
        mode,
    }
    
})();

// event listners
myDom.filter_btn.addEventListener('click',showOption);
myDom.mode.addEventListener('click',changeToDarkMode);


// functions
function showOption(){
    myDom.options.classList.toggle('show');
    myDom.fa_angle_up.style.transform=`rotate(-180deg)`;
}

function changeToDarkMode(){
   document.body.classList.toggle('dark');
}

function render(data){
    const card = `
    <div class="card">
        <img src="${data.flags.png}" alt="">
        <h2 class="countryName">${[...data.name.common].splice(0,10).join('')}</h2>
        <p class="pop"><span>Population</span> ${(data.population/1000000).toFixed(1)}mil</p>
        <p class="rigion"><span>region</span> ${data.region}</p>
        <p class="capital"><span>capital</span> ${data.capital[0]}</p>
    </div>
    `
    myDom.country_card.insertAdjacentHTML('beforeend',card)
}


function fetchData(){
    fetch('https://restcountries.com/v3.1/all')
    .then(Response=>Response.json())
    .then(data=>{
        data.forEach(da=>{
            render(da);
            console.log(da)
        })
    });
}

fetchData()
